import React from 'react';
import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';

import {
    WrapperUpload,
    ButtonUploadBox,
} from '../styles';


const FormularioServicoSobre = ({
    nome,
    setNome,
    descricao,
    setDescricao,
    img,
    setImg,
}) => {

    const hiddenFileInput = React.useRef(null);
    const [file, setFile] = React.useState(null);

    const handleClick = event => {
      hiddenFileInput.current.click();
    };

    const handleChangeFile = (event) => {
        setFile(URL.createObjectURL(event.target.files[0]))
        setImg(event.target.files[0])
    }

    React.useEffect( ()=>{
        if(img){
            setFile(URL.createObjectURL(img))
        }
    },[])


    return (
        <>
            <TextField
            id="descricao-nome"
            label="Nome"
            style={{ margin: '5px 0 10px 0' }}
            placeholder=""
            helperText="Nome do serviço"
            fullWidth
            margin="normal"
            variant="outlined"
            InputLabelProps={{
                shrink: true,
            }}
            value={nome}
            onChange={ (event) => setNome(event.target.value) }
            />
            <TextField
            id="descricao-id"
            label="Descrição"
            style={{ margin:  '5px 0 0 0' }}
            placeholder=""
            helperText="Descrição do serviço"
            multiline
            fullWidth
            margin="normal"
            variant="outlined"
            InputLabelProps={{
                 shrink: true,
            }}
            value={descricao}
            onChange={ (event) => setDescricao(event.target.value) }
        />

        <FormControl>
            <WrapperUpload>
                <ButtonUploadBox onClick={handleClick} src={file} >
                    { file ? <></> :  <AddPhotoAlternateIcon /> }
                </ButtonUploadBox>
                <input type="file"
                        onChange={handleChangeFile}
                        ref={hiddenFileInput}
                        style={{display:'none'}} 
                /> 
            </WrapperUpload> 
            <FormHelperText>Imagem de referência</FormHelperText>
        </FormControl>

    </>
    )

}

export { FormularioServicoSobre } 