import React from 'react';
import { AdicionarServico } from './Adicionar';
import {
    Container,
    WrapperHeader,
    MenuFiltro,
    WrapperAdd,
    WrapperLista,
    GrupoLista,
    GrupoTitulo,
    GrupoItens,
    CardServico,
    WrapperCardMedia,
    useStyles,
} from './styles';

import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';

import Divider from '@material-ui/core/Divider';

// import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';

import Modal from '@material-ui/core/Modal';

import {
    Link
  } from "react-router-dom";

  import { useGenericModel } from '../../hooks';

const Servico = () => {

    const [categorias, setCategoria, CategoriasInicial, addCategoria, rmCategoria, putCategoria, upCategorias] = useGenericModel('account_services_category');
    const [selectCategoria, setSelectCategoria] = React.useState('all');
    const [categoriaLista, setCategoriaLista] = React.useState([]);
    const [open, setOpen] = React.useState(false);

    const handleChange = (event) => {
        setSelectCategoria(event.target.value);
        if(event.target.value === 'all') {
            setCategoriaLista(categorias)
        }else{
            setCategoriaLista([categorias[event.target.value]])
        }
    };

    const handleOpen = () => {
        setOpen(true);
      };
    
    const handleClose = () => {
        setOpen(false);
        upCategorias();
    };

    React.useEffect(()=>{
        setCategoriaLista(categorias)
    },[categorias])

    const classes = useStyles();

    return (
        <Container >
            <WrapperHeader >

                <MenuFiltro >
                    <FormControl className={classes.formControl}>
                        <InputLabel 
                            style={{ 
                                margin: '3px',
                                fontSize: '14px',
                            }}
                        >
                            Categoria
                        </InputLabel>
                        <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={selectCategoria}
                        style={{ 
                            width: '200px'
                        }}
                        variant="outlined"
                        onChange={handleChange}
                        InputLabelProps={{
                            shrink: true,
                        }}
                        >
                        {
                            categorias.filter( (categoria, index) => ( categoria?.servico.length > 0 ? true : false )).length > 0 ?  <MenuItem value={'all'}>Todas</MenuItem> : false
                        }
                        {
                            categorias.map( (categoria, index) => {
                                return categoria?.servico.length > 0 ? <MenuItem value={index}>{categoria.name}</MenuItem> : false
                            })
                        }
                        </Select>
                    </FormControl>
                </MenuFiltro>

                <WrapperAdd >
                    <Button variant="contained" color="primary" onClick={handleOpen} >
                        Adicionar
                    </Button>
                </WrapperAdd>

            </WrapperHeader>
        <WrapperLista>
            {
                categoriaLista.map( (categoria, index) => {
                    return categoria?.servico.length > 0 ? <ServicoItem categoria={categoria} /> : <></>
                })
            }
        </WrapperLista>
        <Modal
                open={open}
                onClose={handleClose}
                className={classes.modal}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
            <AdicionarServico handleClose={handleClose} />
        </Modal>
    </Container>
    );
}

const ServicoItem = ({categoria}) => {
    
    const classes = useStyles();

    return (
        <GrupoLista>
                <GrupoTitulo>
                    <Typography variant="h6">
                        {categoria.name}
                    </Typography>
                </GrupoTitulo>
                <GrupoItens>
                {
                categoria.servico.map((servico, index) => (
                        <CardServico>
                            <div>
                                <CardContent >
                                <Typography variant="h6">
                                    {servico.nome}
                                </Typography>
                                <Typography variant="body2" color="textSecondary">
                                    {servico.descricao}
                                </Typography>
                                </CardContent>
                            </div>
                            <WrapperCardMedia>
                                <CardMedia
                                className={classes.cover}
                                image={servico?.img ? servico?.img : ''}
                                title="Live from space album cover"
                                />
                            </WrapperCardMedia>
                        </CardServico>
                    ))
                }
                </GrupoItens>
        </GrupoLista>
    )

}


export  { Servico };
