import { APIURL, STORAGENAME } from '../environment';
import { adicionarStorage, pegarStorage, removeItemStorage } from './Storage';


    function _PerfilEhValido(perfil){
        if (perfil) {
            if ("access_token" in perfil & "refresh_token" in perfil & "user" in perfil) {
                return true
            }
        }
        return false
    }
  
    function retornaPerfil() {
        let perfil = pegarStorage(STORAGENAME)
        if(_PerfilEhValido(perfil)){
            return perfil
        }
    }
  
    function isAuthenticated() {
        let perfil = pegarStorage(STORAGENAME)
        return _PerfilEhValido(perfil)
    }


    function Login(response){
        return fetch(`${APIURL}/social-login/google/`, {
            method: 'POST',
            headers: new Headers({
                'Content-Type': "application/json"
            }),
            body: JSON.stringify({ 
                'access_token': response.accessToken,
                'code':  response.googleId
            }), 
        })
        .then( res =>  res.json() )
        .then( (dados) => _registraUsuario(dados) )
        .catch( err => _tratandoErro(err) );
    }

    function logout(cb){
        removeItemStorage()
        setTimeout(cb, 200);
    }

    function _registraUsuario(data){
        if(_PerfilEhValido(data)){
            adicionarStorage(STORAGENAME, data) 
            return { result: true}
        }
        return { result: false, err: 'Usuario não é válido'}
    }

    function _tratandoErro(err){
        return { result: false, err}
    }


    function ErroLogin(data){
        console.log(data)  
    }

export { 
        isAuthenticated, 
        Login, 
        ErroLogin, 
        retornaPerfil, 
        logout 
    }