import React from 'react';

import { 
  Button,
  List,
  ListItem,
  ListItemSecondaryAction,
  ListItemText,
  IconButton,
  TextField,
} from '@material-ui/core';

import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import DoneIcon from '@material-ui/icons/Done';

import { WrapperAddCategoria, WrapperScroll } from '../styles';
import { useGenericModel } from '../../../hooks';
//import { atualiza } from '../../utils/Services';

const FormularioServicoCategoria = ({select, setSelect}) => {

  const [categoria, setCategoria, categoriaInicial, addCategoria, rmCategoria, atuCategoria, upCategoria] = useGenericModel('account_services_category');
  const [input, setInput] = React.useState('');
  const [lista, setLista] = React.useState([]);

  React.useEffect(() => {
    setLista(categoria)
  },[categoria])


  function filtro(value, init=false){
      setInput(value)
      let novaLista = [...categoriaInicial]
      setLista( novaLista.filter( item => item?.name.includes(value) ) )      
  }


    return (
        <>
        <TextField
            id="standard-full-width"
            label="Pesquisar"
            style={{ margin: 0 }}
            placeholder=""
            helperText="Pesquise ou adicione categoria"
            fullWidth
            margin="normal"
            variant="outlined"
            InputLabelProps={{
                shrink: true,
            }}
            value={input}
            onChange={ (event) => filtro(event.target.value)  }
        />
        <WrapperScroll>
                  {
                    lista.length > 0 
                    ?
                    <List dense={false}>
                       { lista.map( (item, index) => <FormularioServicoCategoriaItem key={index} item={item} select={select} setSelect={setSelect} /> ) }
                    </List>
                    :
                    <FormularioServicoCategoriaNovoItem inputValue={input} actionAdd={addCategoria} />
                  }
        </WrapperScroll>
        </>
    )
}


const FormularioServicoCategoriaItem = ({item, select, setSelect}) => {


  return (
    <ListItem>
        <ListItemText
          primary={item?.name}
          // secondary={false ? 'Secondary text' : null}
        />
        <ListItemSecondaryAction>
          <IconButton edge="end" aria-label="delete" onClick={ () => setSelect(item?.id) } >
            {
              item?.id === select ?
                <DoneIcon/>
                :
                <CheckBoxOutlineBlankIcon />
            }
          </IconButton>
        </ListItemSecondaryAction>
    </ListItem>
  )

}

const FormularioServicoCategoriaNovoItem = ({inputValue, actionAdd}) => {

  const replaceSpecialChars = (str) => {
    return str.normalize('NFD').replace(/[\u0300-\u036f]/g, '') // Remove acentos
      .replace(/([^\w]+|\s+)/g, '-') // Substitui espaço e outros caracteres por hífen
      .replace(/\-\-+/g, '-')	// Substitui multiplos hífens por um único hífen
      .replace(/(^-+|-+$)/, '') // Remove hífens extras do final ou do inicio da string
      .toLowerCase()
    }

  const envia = () => {
      let novaCategoria = {
        name: inputValue,
        slug: replaceSpecialChars(inputValue)
      }
      actionAdd(novaCategoria);
  }

  return (
    <>
        {
          inputValue.length > 0 ?
            <WrapperAddCategoria >
                <Button variant="contained" color="primary" onClick={envia} >
                        Adicionar
                </Button>
            </WrapperAddCategoria>
          :
          <></>
        }
    </>
  )

}


export { FormularioServicoCategoria }