import React from 'react';
import { GOOGLECLIENTID } from '../../environment';
import { Login, ErroLogin } from '../../utils/Auth';

import GoogleLogin from 'react-google-login';
import { useHistory, useLocation } from "react-router-dom";

import {
    Container,
    Wrapperleft,
    WrapperRight,
    WrapperLogin,
    WrapperSocial,
    WrapperInput,
    WrapperOption,
    WrapperAdd,
    IconGoogle,
    IconLogIn
} from './styles';

import TextField from '@material-ui/core/TextField';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';

import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';

const LoginPage = () => {

    let history = useHistory();
    let location = useLocation();
    let { from } = location.state || { from: { pathname: "/" } };
    
    const __Login = (googleResp) => {
        Login(googleResp).then( resp => {
            if(resp.result){
                history.replace(from);
            }else{
                console.log('Erro no login')
            }
        })
    }

    return (
        <Container >
            <Wrapperleft >
            </Wrapperleft>
            <WrapperRight>

                <WrapperLogin>
                    
                    <WrapperSocial >
                        
                        <GoogleLogin
                            clientId={GOOGLECLIENTID}
                            buttonText="Login"
                            onSuccess={__Login}
                            onFailure={ErroLogin}
                            cookiePolicy={'single_host_origin'}
                            render={renderProps => (
                                <Button
                                variant="contained"
                                color="secondary"
                                startIcon={<IconGoogle />}
                                onClick={renderProps.onClick} 
                                disabled={renderProps.disabled}
                                style={{ width: '100%' }}
                              >
                                    Google
                              </Button>
                              )}
                        />

                    </WrapperSocial>
                    <Divider />
                    <WrapperInput >         
                        <TextField
                            id="descricao-nome"
                            label="Email"
                            style={{ margin: '5px 0 10px 0' }}
                            placeholder=""
                            helperText="Email cadastrado"
                            fullWidth
                            margin="normal"
                            variant="outlined"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            // value={nome}
                            // onChange={ (event) => setNome(event.target.value) }
                        />
                        <TextField
                                id="descricao-id"
                                label="Senha"
                                style={{ margin:  '5px 0 0 0' }}
                                placeholder=""
                                helperText="Senha cadastrado"
                                multiline
                                fullWidth
                                margin="normal"
                                variant="outlined"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                        />
                        <WrapperOption >
                            <WrapperAdd >
                                <Button 
                                    startIcon={<IconLogIn/>}
                                    variant="contained" 
                                    color="primary" 
                                    style={{ width: '100%' }} 
                                    >
                                    Sign In
                                </Button>
                            </WrapperAdd>
                            <WrapperAdd >
                                <Typography variant="overline" display="block" gutterBottom  style={{ fontSize: '10px' }} >
                                    Esqueceu a senha ?
                                </Typography>
                            </WrapperAdd>
                        </WrapperOption>
                    </WrapperInput>
                </WrapperLogin>
            </WrapperRight>
        </Container>
    );
}

export { LoginPage };
