import { APIURL, STORAGENAME } from '../environment';

import { adicionarStorage, adicionarStoragelist, removeItemStorage, } from './Storage';
import { retornaPerfil} from './Auth';
import { CustomGet } from './customGet';


const Path = {
    account_register: "/account/register/",
    account_profile: "/account/profile/",
    account_card: "/account/card/",
    account_client_list: "/account/client/list/",
    account_services_category: "/account/services_category/",
    account_services: "/account/services/",
    account_days_week: "/account/days_week/",
    account_name: "/account/name/",
    account_events: "/account/events/",
    token_refresh: "/api/token/refresh/"
} 


function retornaPathName(pathName, uuid, customGet=false){
    return  APIURL + Path[pathName]
}  

//Lidando com resposta de renovação
async function _lidarRenovacao(resp){
    switch(resp.status) {
        case 200:
            return resp
        case 401:
            // resp.json().then( async data => {
            //     console.log(data)
            // })
            removeItemStorage()
            throw new Error('Renocação não pode ser concluida');
        default:
            resp.json().then( async data => {
                console.log(data)
            })
            throw new Error('Aconteceu algum erro ao executar a renovação');
    }
}

//Ação de renovar
async function _renovar(){
    let perfil = retornaPerfil()
    let formData = new FormData();
    console.log('[Renovando...]');

    formData.append('refresh', perfil.refresh_token);
    return fetch(APIURL + Path['token_refresh'], {
        headers: new Headers({
            'Accept': "application/json"
          }), 
        method: 'POST',
        body: formData,
    })
    .then( async res => await _lidarRenovacao(res))
    .catch(err => console.log(err));
}

//lista de açoes que podemos tomar
const requestCode = {
    token_not_valid: _renovar,
}

//Lidando com a resposta
async function _lidarResposta(resp){
    switch(resp.status) {
        case 200:
            return resp
        case 201:
            return resp
        case 204:
            return resp
        case 401:
            //Melhorar o tratamento via codigo
            return resp.json().then( async data => {
                // console.log(data)
                const lidar = requestCode[data.code]
                return await lidar()
            })
        default:
            // console.log(resp.status)
            resp.json().then( async data => {
                console.log(data)
            })
            throw new Error(`Aconteceu algum erro ao executar a requisição`);
    }
}

//
// GET
//
function realizarPedido(pathName, perfil){
    return fetch(pathName, {
        method: 'GET',
        headers: new Headers({
            'Authorization': `Bearer ${perfil.access_token}`,
          }), 
    })
    .then( async res => await _lidarResposta(res))
    .then(  async res =>  res.json() )
    .then( (dados) => dados)
    .catch(err => console.log(err));
}

/**
 * @param {string} pathName - string do nome do model 
 * @param {string} uuid - string uuid
 * @param {Array} uuid - Lista de CustomGet
 */

//pegar itens da API
async function pegar(pathName, customGet=false){
    let perfil = retornaPerfil()
    let resultado = await realizarPedido(retornaPathName(pathName, customGet), perfil)
    if( resultado ){
        if("access" in resultado){
            //console.log(resultado);
            perfil.access_token = resultado.access
            adicionarStorage(STORAGENAME, perfil)
            resultado = await realizarPedido(retornaPathName(pathName, customGet), perfil)
            return resultado
        }
    }
    return resultado
}

//
// POST
//

function criaFormulario(form){
    if( form instanceof FormData ){
        return form
    }
    const formData = new FormData();
    for(var key in form){
        formData.append(key, typeof form[key] === 'object' && form[key] instanceof File === false ? JSON.stringify(form[key]) : form[key] )
    }
    return formData
}

function realizarPedidoEnvio(pathName, perfil, form){
    return fetch(pathName, {
        method: 'post',
        body: form,
        headers: new Headers({
            'Authorization': `Bearer ${perfil.access_token}`,
          }), 
    })
    .then( async res => await _lidarResposta(res))
    .then(  async res =>  res.json() )
    .then( (dados) => dados)
    .catch(err => console.log(err) );
}

async function envia(pathName, form){
        let perfil = retornaPerfil()
        let resultado = await realizarPedidoEnvio(retornaPathName(pathName), perfil, criaFormulario(form))
        if( resultado ){
            if("access" in resultado){
                perfil.access_token = resultado.access
                adicionarStorage(STORAGENAME, perfil)
                resultado = await realizarPedidoEnvio(retornaPathName(pathName), perfil, criaFormulario(form))
                return resultado
            }
        }
        return resultado
}

//------------------------------------------------------------------------------------//
//
// PUT
//
function realizarPedidoAtualiza(pathName, perfil, form){
    return fetch(pathName, {
        method: 'PUT',
        body: form,
        headers: new Headers({
            'Authorization': `Bearer ${perfil.access_token}`,
          }), 
    })
    .then( async res => await _lidarResposta(res))
    .then(  async res =>  res.json() )
    .then( (dados) => dados)
    .catch(err => console.log(err));
}

async function atualiza(pathName, form, modelId=false){
    let perfil = retornaPerfil()
    let resultado = await realizarPedidoAtualiza(retornaPathName(pathName, modelId), perfil, criaFormulario(form))
    if( resultado ){
        if("access" in resultado){
            perfil.access_token = resultado.access
            adicionarStorage(STORAGENAME, perfil)
            resultado = await realizarPedidoAtualiza(retornaPathName(pathName, modelId), perfil, criaFormulario(form))
            return resultado
        }
    }
    return resultado
}
//------------------------------------------------------------------------------------//

//
// DELL
//
function realizarPedidoRemover(pathName, perfil, form){
    return fetch(pathName, {
        method: 'DELETE',
        headers: new Headers({
            'Authorization': `Bearer ${perfil.access}`,
          }), 
    })
    .then( async res => await _lidarResposta(res))
    .then(  async res => ({status: true, ...res}) )
    .catch(err => console.log(err));
}

async function remove(pathName, modelId){
    let perfil = await retornaPerfil()
    let path = retornaPathName(pathName) + modelId + "/";
    let resultado = await realizarPedidoRemover(path, perfil)
    if( resultado ){
        if("access" in resultado){
            perfil.access = resultado.access
            adicionarStorage(STORAGENAME, perfil)
            resultado = await realizarPedidoRemover(retornaPathName(pathName), perfil)
            return resultado
        }
    }
    return resultado
}


export { pegar, envia, atualiza, remove }