import React from 'react';
import {
    Switch,
    Route,
    useRouteMatch,
    Redirect,
  } from "react-router-dom";
  
import { CustomDrawer } from '../components';
import { Home, Servico, Cartao, Perfil, Agenda, Conta } from '../screens';

import { APPURL } from '../environment';

const Dashboard = () => {

    let match = useRouteMatch();

    return (
        <CustomDrawer >
            <Switch>
                <Route path="/co">
                    <Conta />
                </Route>
                <Route path="/ct">
                    <Cartao />
                </Route>
                <Route path="/a">
                    <Agenda />
                </Route>
                <Route path="/p">
                    <Perfil />
                </Route>
                <Route path="/s">
                    <Servico />
                </Route>
                <Route path={match.path}>
                    <Home />
                </Route>
            </Switch>
        </CustomDrawer>
    );
}

export default Dashboard;
