import React, { useState, useEffect } from "react";
import { pegar, envia, atualiza, remove } from '../utils/Services'; 

async function pegaModel(nameModel, callback, setInicial, customGet) {
    let result = await pegar(nameModel, true, customGet)
    callback(result)
    setInicial(result)
}

async function enviaModel(nameModel, form, callback) {
    let result = await envia(nameModel, form)
    callback(result)
}

async function atualizaModel(nameModel, form, modelId, callback) {
    let result = await atualiza(nameModel, form, modelId)
    callback(result)
}

async function removeModel(nameModel, modelId, callback) {
    let result = await remove(nameModel, modelId)
    if( result.status ){
        callback(modelId)
    }
}


function useGenericModel(nameModel, customGet=false){
    
    const [inicial, setInicial] = useState([]);
    const [model, setModel] = useState([]);

    // const [update, setUpdate] = useState({});
    
    useEffect(() => {
        pegaModel(nameModel, setModel, setInicial, customGet)
    },[]);


    function addLista(data){
        setInicial([...inicial, data])
        setModel([...inicial, data])
    }

    function atualizaLista(data){
        setInicial( inicial.map( (item) => item.id === data.id ? data : item) )
        setModel( inicial.map( (item) => item.id === data.id ? data : item) )
    }

    function removeLista(id){
        setInicial( inicial.filter( (item) => item.id !== id ) )
        setModel( model.filter( (item) => item.id !== id ) )
    }


    async function atualiza(data, modelId){
        await atualizaModel(nameModel, data, modelId, atualizaLista)
    }    

    function adicionar(data){
        enviaModel(nameModel, data, addLista)
    }

    function remover(modelId){
        removeModel(nameModel, modelId,  removeLista)
    }

    function update(newCustomGet=false){
        pegaModel(nameModel, setModel, setInicial, newCustomGet)
    }

    return [model, setModel, inicial, adicionar, remover, atualiza, update];
}


function useGenericRemove(){
   
    function remover(nameModel, modelId){
        removeModel(nameModel, modelId, (resp) => console.log(resp))
    }

    return [remover];
}



//Lista de looks 
function useLista(){

    const [lista, setlista] = useState([]);
    
    function adicionar(data){
        setlista([...lista, data])
    }

    function remover(data){
        let novaLista = lista;
        novaLista.splice(novaLista.indexOf(data), 1)
        setlista(novaLista)
    }

    function LidandoMudança(data){
        let novaLista = lista;
        if( novaLista.indexOf(data) > -1 ){
            remover(data)
        }else{
            adicionar(data);
        }
    }

    return [lista, LidandoMudança];
}


export { 
        useGenericModel, 
        useLista, 
        enviaModel, 
        pegaModel, 
        useGenericRemove
     }