function adicionarStorage(name, data){
    return localStorage.setItem(name, JSON.stringify(data))
}

function adicionarStoragelist(name, data){
    try {
        let atualLista = pegarStorage(name)
        atualLista =  atualLista ? [...atualLista, data] : [data]
        return localStorage.setItem(name, JSON.stringify(atualLista))
    } catch (e) {
        console.log(e)
    }
}


function pegarStorage(name){
    let dataJson = localStorage.getItem(name);
    return JSON.parse(dataJson);
}

function removeItemStorage(){
    return localStorage.clear();
}


export { adicionarStorage, pegarStorage, removeItemStorage, adicionarStoragelist }