import React from 'react';

import {
        FormularioServicoCategoria, 
        FormularioServicoSobre,
        FormularioServicoTempo,
        FormularioServicoConcluir 
    } from './formulario';

import {
    ModalContent,
    WrapperFormulario,
    useStyles,
} from './styles';

import { useTheme } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import { useGenericModel } from '../../hooks';

export function TextMobileStepper({handleClose}) {

  const [servico, setServico, ServicoInicial, addServico, rmServico, atuServico, upServico] = useGenericModel('account_services');

  const [categoria, setCategoria] = React.useState(null);
  const [nome, setNome] = React.useState('');
  const [descricao, setDescricao] = React.useState('');
  const [img, setImg] = React.useState(null);
  const [tipoServico, setTipoServico] = React.useState('');
  const [tempoAtividade, setTempoAtividade] = React.useState('');
  const [tmpDeslocamento, setTmpDeslocamento] = React.useState('');
  const [atendimentoSemana, setAtendimentoSemana] = React.useState([]);
  
    const tutorialSteps = [
        {
            label: 'Selecione ou crie uma categoria',
            componet: <FormularioServicoCategoria 
                        select={categoria} 
                        setSelect={setCategoria} />
        },
        {
            label: 'Descreva seu serviço',
            componet: <FormularioServicoSobre 
                        nome={nome}  
                        setNome={setNome} 
                        descricao={descricao} 
                        setDescricao={setDescricao} 
                        img={img} 
                        setImg={setImg} />
        },
        {
            label: 'Algumas informações sobre atendimento',
            componet: <FormularioServicoTempo 
                        tipoServico={tipoServico} 
                        setTipoServico={setTipoServico} 
                        tempoAtividade={tempoAtividade} 
                        setTempoAtividade={setTempoAtividade} 
                        tmpDeslocamento={tmpDeslocamento} 
                        setTmpDeslocamento={setTmpDeslocamento} 
                        atendimentoSemana={atendimentoSemana} 
                        setAtendimentoSemana={setAtendimentoSemana}
                        />
        },
        {
            label: 'Concluir:',
            componet: <FormularioServicoConcluir 
                  categoria={categoria}
                  nome={nome}
                  descricao={descricao}
                  img={img}
                  tipoServico={tipoServico}
                  tempoAtividade={tempoAtividade}
                  tmpDeslocamento={tmpDeslocamento}
                  atendimentoSemana={atendimentoSemana}          
            />
        },
    ];

  const classes = useStyles();
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = tutorialSteps.length;

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleConcluir = () => {
    let novoServico = {
      categoria: categoria,
      nome: nome,
      descricao: descricao,
      img: img,
      tipo: tipoServico,
      tempo_atividade: tempoAtividade,
      tempo_deslocamento: tmpDeslocamento,
      semana: atendimentoSemana.filter( item => item.checked ),
    }  
    addServico(novoServico)
    setTimeout(() => {
      handleClose()
    }, 500);
  };

  return (
    <div className={classes.root}>
      <Paper square elevation={0} className={classes.header}>
        <Typography>{tutorialSteps[activeStep].label}</Typography>
      </Paper>
        <WrapperFormulario>
            {tutorialSteps[activeStep].componet}
        </WrapperFormulario>
      <MobileStepper
        steps={maxSteps}
        position="static"
        variant="text"
        activeStep={activeStep}
        nextButton={
          <Button size="small" onClick={ activeStep === maxSteps - 1 ?  handleConcluir : handleNext} >
            {/* disabled={activeStep === maxSteps - 1} */}
            { activeStep === maxSteps - 1 ? 'Concluir' : "Avançar" }
            {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            Voltar
          </Button>
        }
      />
    </div>
  );
}

const AdicionarServico = ({handleClose}) => {

    return (
        <ModalContent >
            <TextMobileStepper handleClose={handleClose} />
        </ModalContent>
    );

}

export  { AdicionarServico };