import React from 'react';

import { Calendar, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'

const localizer = momentLocalizer(moment)


const Agenda = () => {

  const now = new Date();

  return (
    <div>
        <Calendar
            localizer={localizer}
            events={[
                {   
                    title: 'Teste',
                    start: new Date(now.getFullYear(), now.getMonth(), now.getDate(), 20, 0),
                    end: new Date(now.getFullYear(), now.getMonth(), now.getDate(), 21, 0),
                    allDay: false,
                    resource: [],
                }
            ]}
            startAccessor="start"
            endAccessor="end"
            style={{ height: 500 }}
            defaultView="day"
        />
    </div>
  );
}

export { Agenda }
