import React from 'react';

import {
    CardServicoConcluir,
    WrapperCardMedia,
    useStyles
}
from '../styles';

import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';


const FormularioServicoConcluir = ({
    categoria,
    nome,
    descricao,
    img,
    tipoServico,
    tempoAtividade,
    tmpDeslocamento,
    atendimentoSemana
}) => {

    const classes = useStyles();
    const [file, setFile] = React.useState(false);

    React.useEffect( () => {
        if(img){
            setFile(URL.createObjectURL(img))
        }
    },[img])


    return (
        <CardServicoConcluir>
            <div>
                <CardContent >
                <Typography variant="h6">
                    {nome}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    {descricao}
                </Typography>
                </CardContent>
            </div>
            <WrapperCardMedia>
                <CardMedia
                className={classes.cover}
                image={file ? file : ''}
                title="Live from space album cover"
                />
            </WrapperCardMedia>
        </CardServicoConcluir>
    )
}

export { FormularioServicoConcluir } 