import React from 'react';

import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import TextField from '@material-ui/core/TextField';

import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormLabel from '@material-ui/core/FormLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import { makeStyles } from '@material-ui/core/styles';

import { useGenericModel } from '../../../hooks';

const useStyles = makeStyles((theme) => ({
    formControl: {
      minWidth: '100%',
    }
  }));


const FormularioServicoTempo = ({
    tipoServico,
    setTipoServico,
    tempoAtividade,
    setTempoAtividade,
    tmpDeslocamento,
    setTmpDeslocamento,
    atendimentoSemana,
    setAtendimentoSemana,
}) => {

    const classes = useStyles();
    const [diasSemana, setDiasSemana, DiasSemanaInicial] = useGenericModel('account_days_week');

    React.useEffect( () => {
        initChecked()
    },[diasSemana])

    const handleChange = (event) => {
        let novaLista = atendimentoSemana.map( item => {
                            item.id === event.target.name ? item.checked = !item.checked : item.checked = item.checked;
                            return item 
                        })
        setAtendimentoSemana(novaLista);
    };

    const initChecked = () => {
        if(atendimentoSemana.length === 0){
            let novalista = diasSemana.map( (item) =>{         
                return {...item, 'checked': false}
            })
            setAtendimentoSemana(novalista)
        }
    }

    return (
        <>
            <FormControl 
                className={classes.formControl} 
                variant="outlined" 
                style={{ margin: '5px 0 8px 0' }}
                >
                <InputLabel htmlFor="age-native-simple">Tipo</InputLabel>
                    <Select
                            native
                            value={tipoServico}
                            onChange={(event) => setTipoServico(event.target.value)  }
                            inputProps={{
                                name: 'tipo',
                                id: 'tipo-de-servico',
                            }}
                            >
                            <option aria-label="None" value="" />
                            <option value={'0'}>Local</option>
                            <option value={'1'}>Visita</option>
                            <option value={'3'}>Chamada</option>
                    </Select>
                <FormHelperText>Modalidade do serviço</FormHelperText>
            </FormControl>
            <form noValidate>
                <TextField
                    id="tmp-atividade"
                    label="Tempo de atividade"
                    type="time"
                    fullWidth
                    defaultValue="00:30"
                    variant="outlined"
                    style={{ margin: '5px 0 8px 0' }}
                    // className={classes.textField}
                    InputLabelProps={{
                    shrink: true,
                    }}
                    inputProps={{
                    step: 300, // 5 min
                    }}
                    value={tempoAtividade}
                    onChange={ (event) => setTempoAtividade(event.target.value) }
                />
            </form>
            <form noValidate>
                <TextField
                    id="tmp-deslocamento"
                    label="Tempo de Deslocamento"
                    type="time"
                    fullWidth
                    defaultValue="00:00"
                    variant="outlined"
                    style={{ margin: '5px 0 8px 0' }}
                    // className={classes.textField}
                    InputLabelProps={{
                    shrink: true,
                    }}
                    inputProps={{
                    step: 300, // 5 min
                    }}
                    value={tmpDeslocamento}
                    onChange={ (event) => setTmpDeslocamento(event.target.value) }
                />
            </form>
        <FormControl>
        <FormHelperText>Atendimento na semana</FormHelperText>
            <FormGroup row >
                    {
                       atendimentoSemana.map( (item, index) => (
                        <FormControlLabel
                                control={
                                <Checkbox
                                    icon={<CheckBoxOutlineBlankIcon fontSize="small" />}
                                    checkedIcon={<CheckBoxIcon fontSize="small" />}
                                    name={item?.id}
                                    checked={item?.checked}
                                    onChange={handleChange}
                                />
                                }
                                label={item?.name}
                            />
                       ))
                    }
            </FormGroup>
        </FormControl>
        </>
    )

}

export { FormularioServicoTempo } 